import React from "react";
import { Map, Marker, GoogleApiWrapper } from "google-maps-react";


const mapStyles = {
   width: '90%',
   height: '100%',
   margin:'auto'
};

const containerStyle = { width: '100%',
height: '83%', position:'absolute' }

class MapContainer extends React.Component {
 
    render() { 
        return ( <Map
            style={mapStyles}
            google={this.props.google}
            containerStyle={containerStyle}
            zoom={5}
            initialCenter={this.props.cords}
            center={this.props.cords}
          >
            <Marker position={this.props.cords} />
          </Map>);
    }
}
 

export default GoogleApiWrapper({
  apiKey: "AIzaSyAmMVrJwjsXHoL7O0IgWkBQb-oEbaoHuPY",
})(MapContainer);
